
.App {
  text-align: center;
  #quote-box {
    padding: 25px;
    background-color: white;
    border-radius: 10px;
    color: black;
    #author{
      text-align: end;
    }
    .button{
     #tweet-quote{
      float: left;
      padding: 10px;
      color: white;
      text-decoration: none;
      border-radius: 10px;

    }
  }
    #new-quote{
      float: right;
      color: white;
      border-radius: 10px;
      padding: 10px;
      border: none;
      cursor: pointer;
    }
  }
  .App-header {
    background-color: #d61e1e;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
  }
  
}








